@import "global/01-mixins";
@import "fonts";
@import "global/03-vars";
html {
    box-sizing: border-box;
}

body {
    font-family: 'Montserrat', sans-serif;
    font-size: 20px;
    line-height: 1;
    font-weight: 400;
    color: #252525;
    position: relative;
    height: 100%;
    background: #fff;
    -webkit-tap-highlight-color: transparent;
}

div,
section,
footer,
.btn {
    box-sizing: border-box;
}

a {
    color: #fff;
    -webkit-transition: all 0.2s linear;
    -moz-transition: all 0.2s linear;
    -ms-transition: all 0.2s linear;
    -o-transition: all 0.2s linear;
    transition: all 0.2s linear;
    text-decoration: none;
}

a:hover {
    text-decoration: none;
}

a:focus,
input:focus {
    outline: none;
}

p {
    font-size: 18px;
    line-height: 186.5%;
    color: #000000;
    font-weight: normal;
    margin: 0 0 10px 0;
}

input {
    display: block;
    width: 100%;
    font-size: 30px;
    font-weight: 700;
    line-height: 29px;
    padding: 0 8px;
    margin-bottom: 20px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    -webkit-transition: all 0.2s linear;
    -moz-transition: all 0.2s linear;
    -ms-transition: all 0.2s linear;
    -o-transition: all 0.2s linear;
    transition: all 0.2s linear;
}

input:hover,
input:focus {
    outline: none;
}

ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

ul li {
    padding: 0 0 0 0;
}

button {
    border: none;
    outline: none;
    cursor: pointer;
    -webkit-transition: all 0.2s linear;
    -moz-transition: all 0.2s linear;
    -ms-transition: all 0.2s linear;
    -o-transition: all 0.2s linear;
    transition: all 0.2s linear;
}

.title {
    color: #fff;
    font-size: 42px;
    font-weight: 700;
    line-height: 53px;
    margin-bottom: 20px;
}

.desc {
    color: #252525;
    font-size: 20px;
    font-weight: 400;
    line-height: 29px;
    margin-bottom: 15px;
}

img {
    max-width: 100%;
    max-height: 100%;
    display: block;
}

.clearfix:after {
    content: '';
    display: table;
    clear: both;
}

.wrapper {
    position: relative;
}

.duga {
    position: absolute;
    top: 0;
    right: 0;
    z-index: -1;
}

.text-vertical {
    position: absolute;
    top: 80px;
    z-index: -1;
    font-weight: 900;
    font-size: 173px;
    line-height: 92%;
    text-transform: uppercase;
    color: #F5F5F5;
    pointer-events: none;
    transform: rotate(-90deg) translateY(-334px);
    -webkit-transform: rotate(-90deg) translateY(-334px);
    -moz-transform: rotate(-90deg) translateY(-334px);
    -ms-transform: rotate(-90deg) translateY(-334px);
    -o-transform: rotate(-90deg) translateY(-334px);
}

.container {
    max-width: 1562px;
    width: 100%;
    padding: 0 16px;
    margin: 0 auto;
}

input:not([type=checkbox]):not([type=radio]),
textarea,
select {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}

header {
    padding: 45px 0 0 0;
    .container {
        display: flex;
        justify-content: flex-start;
        align-items: center;
    }
}

.tel {
    justify-self: flex-end;
    font-weight: 300;
    font-size: 18px;
    line-height: 100%;
    text-align: left;
    padding: 3px 0 3px 35px;
    color: #000000;
    background: url(../img/tel.svg) no-repeat left center;
    margin-left: auto;
    b {
        font-weight: 600;
    }
}

.logo {
    font-weight: 800;
    font-size: 25px;
    font-style: italic;
    line-height: 100%;
    color: #000000;
    padding: 4px 0 4px 48px;
    background: url(../img/logo-ico.svg) no-repeat left center;
    margin-right: 78px;
}

.address {
    font-weight: 300;
    font-size: 18px;
    line-height: 100%;
    color: #000000;
    padding: 3px 0 3px 28px;
    background: url(../img/gps.svg) no-repeat left center;
    margin-right: 85px;
    b {
        font-weight: 600;
    }
}

.timework {
    font-weight: 300;
    font-size: 18px;
    line-height: 100%;
    color: #000000;
    padding: 3px 0 3px 36px;
    background: url(../img/clock.svg) no-repeat left center;
}

.top-wrap {
    position: relative;
    padding: 126px 0 0 0;
    min-height: 767px;
    h1 {
        font-weight: 800;
        font-size: 50px;
        line-height: 111.5%;
        color: #000000;
        margin-bottom: 27px;
        width: 60%;
    }
    p {
        width: 44%;
    }
    b {
        font-weight: 700;
    }
}

.top-img {
    position: absolute;
    bottom: 0;
    right: 171px;
    z-index: -1;
}

section {
    .title {
        position: relative;
        width: fit-content;
        font-weight: 600;
        font-size: 40px;
        line-height: 111.5%;
        color: #000000;
        margin-bottom: 42px;
        &::after {
            content: '';
            display: block;
            width: 57px;
            height: 5px;
            background: #FF4C00;
            position: absolute;
            top: 23px;
            right: -82px;
        }
    }
}

.servises {
    padding: 120px 0 148px 0;
    background: #F5F5F5;
}

.servises-items {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-wrap: wrap;
}

.servises-item {
    position: relative;
    z-index: 5;
    max-width: 360px;
    overflow: hidden;
    width: 100%;
    min-height: 250px;
    margin-right: 30px;
    margin-bottom: 30px;
    background: #FFFFFF;
    border-radius: 24px;
    padding: 60px 20px 0 50px;
    &::after {
        content: '';
        display: block;
        width: 141px;
        height: 100px;
        position: absolute;
        right: 0;
        bottom: 0;
        background: url(../img/serv-ico.png) no-repeat center;
        z-index: -2;
    }
    &:nth-child(4n) {
        margin-right: 0;
    }
    a {
        display: block;
        max-width: 139px;
        width: 100%;
        padding: 14px 0 13px 0;
        background: #FF4C00;
        border-radius: 19px;
        font-weight: 600;
        font-size: 13px;
        line-height: 100%;
        text-align: center;
        letter-spacing: 0.11em;
        color: #FFFFFF;
    }
    img {
        @include tr;
        position: absolute;
        right: 33px;
        bottom: 28px;
        z-index: -1;
    }
    img.hide-img {
        opacity: 0;
    }
}

.servises-title {
    @include tr;
    font-weight: 500;
    font-size: 20px;
    line-height: 112%;
    color: #000000;
    margin-bottom: 36px;
}

.works {
    padding: 90px 0 145px 0;
    .title {
        margin-bottom: 54px;
    }
}

.map {
    position: relative;
    min-height: 629px;
    .container {
        position: relative;
        top: 174px;
        z-index: 5;
    }
}

.map-contacts {
    position: absolute;
    left: 16px;
}

footer {
    padding: 44px 0 0 0;
    background: #FAFAFA;
    .tel {
        font-size: 20px;
    }
}

.footer-top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 35px;
    border-bottom: 1px solid #EDEDED;
}

.works-box {
    width: 50%;
    position: relative;
    height: 0;
    padding-bottom: 42%;
    img {
        position: absolute;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
    }
}

.cascade-slider_item {
    @include tr;
    position: relative;
    border-radius: 24px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1216px;
    width: 100%;
    outline: none;
    overflow: hidden;
    z-index: 0;
    opacity: 1;
}

.slick-initialized .slick-slide.works-slider__item {
    @include tr;
    position: relative;
    border-radius: 24px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 500px;
    width: 100%;
    outline: none;
    overflow: hidden;
    z-index: 0;
    opacity: 1;
    &:after {
        content: '';
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        opacity: 1;
        background: linear-gradient(180deg, #FFFFFF 0%, rgba(255, 255, 255, 0) 100%);
        z-index: 0;
        @include tr;
        border-radius: 24px;
    }
}

.countsSlides span {
    font-size: 20px;
    color: #000;
}

.works-slider {
    .slick-arrow {
        position: absolute;
        @include tr;
        width: 64px;
        height: 64px;
        top: 51.5%;
        font-size: 0;
        z-index: 2;
        background: rgba(0, 0, 0, 0.13);
        border-radius: 50%;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        -ms-border-radius: 50%;
        -o-border-radius: 50%;
        transform: translateY(-50%);
        -webkit-transform: translateY(-50%);
        -moz-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
        -o-transform: translateY(-50%);
        &:hover {
            background: rgba(0, 0, 0, 0.35);
        }
    }
    .slick-prev {
        left: -32px;
        &.disable {
            pointer-events: none;
            background: rgba(0, 0, 0, 0.05);
        }
        &::before {
            content: '';
            position: absolute;
            top: 0;
            right: 0;
            left: 0;
            bottom: 0;
            margin: auto;
            background: url(../img/prev.svg) no-repeat center;
        }
    }
    .slick-next {
        right: -32px;
        &::before {
            content: '';
            position: absolute;
            top: 0;
            right: 0;
            left: 0;
            bottom: 0;
            margin: auto;
            background: url(../img/next.svg) no-repeat center;
        }
    }
}

.works-before {
    position: relative;
    &::after {
        content: '';
        display: block;
        width: 2px;
        height: 100%;
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        background: #fff;
    }
}

.copyright {
    padding-top: 56px;
    padding-bottom: 62px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    & * {
        font-weight: normal;
        font-size: 10px;
        line-height: 100%;
        color: #676767;
    }
    a {
        margin-right: 30px;
    }
    .gen {
        margin-right: 0;
        margin-left: auto;
    }
}

.map-contacts {
    padding: 55px 50px 25px 60px;
    background: #FFFFFF;
    max-width: 367px;
    width: 100%;
}

.map-contacts-item {
    margin-bottom: 30px;
    b {
        display: block;
        font-weight: 500;
        font-size: 16px;
        line-height: 100%;
        color: #000000;
        margin-bottom: 7px;
    }
    span,
    a {
        display: block;
        font-weight: normal;
        font-size: 14px;
        line-height: 100%;
        color: #000000;
    }
}

.cascade-slider_container {
    position: relative;
    max-width: 1216px;
    width: 100%;
    height: 511px;
    margin: 0 auto;
}

.cascade-slider_item {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateY(-50%) translateX(-50%) scale(0.3);
    transition: all .5s ease;
    opacity: 0;
    z-index: -1;
    -webkit-transition: all .5s ease;
    -moz-transition: all .5s ease;
    -ms-transition: all .5s ease;
    -o-transition: all .5s ease;
}

.cascade-slider_item.next {
    left: 128%;
    transform: translateY(-50%) translateX(-150%) scale(0.82432);
    opacity: 0.5;
    z-index: 1;
    -webkit-transform: translateY(-50%) translateX(-150%) scale(0.82432);
    -moz-transform: translateY(-50%) translateX(-150%) scale(0.82432);
    -ms-transform: translateY(-50%) translateX(-150%) scale(0.82432);
    -o-transform: translateY(-50%) translateX(-150%) scale(0.82432);
}

.cascade-slider_item.prev {
    left: -28%;
    transform: translateY(-50%) translateX(50%) scale(0.82432);
    opacity: 0.5;
    z-index: 1;
    -webkit-transform: translateY(-50%) translateX(50%) scale(0.82432);
    -moz-transform: translateY(-50%) translateX(50%) scale(0.82432);
    -ms-transform: translateY(-50%) translateX(50%) scale(0.82432);
    -o-transform: translateY(-50%) translateX(50%) scale(0.82432);
}

.cascade-slider_item.now {
    top: 50%;
    left: 50%;
    transform: translateY(-50%) translateX(-50%) scale(1);
    opacity: 1;
    z-index: 5;
}

.cascade-slider_arrow {
    display: inline-block;
    position: absolute;
    top: 50%;
    cursor: pointer;
    z-index: 5;
}

.cascade-slider_arrow-left {
    left: -194px;
    position: absolute;
    transition: all .2s linear;
    -webkit-transition: all .2s linear;
    -moz-transition: all .2s linear;
    -ms-transition: all .2s linear;
    -o-transition: all .2s linear;
    width: 64px;
    height: 64px;
    top: 51.5%;
    font-size: 0;
    z-index: 2;
    background: rgba(0, 0, 0, 0.13);
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
    &:before {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        margin: auto;
        background: url(../img/prev.svg) no-repeat center;
    }
}

.cascade-slider_arrow-right {
    right: -194px;
    position: absolute;
    transition: all .2s linear;
    -webkit-transition: all .2s linear;
    -moz-transition: all .2s linear;
    -ms-transition: all .2s linear;
    -o-transition: all .2s linear;
    width: 64px;
    height: 64px;
    top: 51.5%;
    font-size: 0;
    z-index: 2;
    background: rgba(0, 0, 0, 0.13);
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
    &:before {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        margin: auto;
        background: url(../img/next.svg) no-repeat center;
    }
}

.cascade-slider_nav {
    position: absolute;
    bottom: -120px;
    width: 100%;
    text-align: center;
    z-index: 5;
}

.cascade-slider_dot {
    display: inline-block;
    width: 1em;
    height: 1em;
    margin: 1em;
    background: #ddd;
    list-style: none;
    cursor: pointer;
}

.cascade-slider_dot:hover {
    background: #555;
}

.cascade-slider_dot.cur {
    background: #555;
}


/* I E   S T Y L E S */

@media all and (-ms-high-contrast: none),
(-ms-high-contrast: active) {
    .block {
        width: 100%;
    }
}

.ov-hidden {
    overflow: hidden;
}

.close:not(:disabled):not(.disabled) {
    cursor: pointer
}

@media screen and (min-width:992px) {
    .cascade-slider_arrow-right:hover {
        background: rgba(0, 0, 0, 0.3);
    }
    .cascade-slider_arrow-left:hover {
        background: rgba(0, 0, 0, 0.3);
    }
    .servises-item:hover {
        background: #FF4C00;
    }
    .servises-item:hover a {
        background: #fff;
        color: #000000;
    }
    .servises-item:hover .servises-title {
        color: #fff;
    }
    .servises-item:hover::after {
        opacity: 0;
    }
    .servises-item:hover img {
        opacity: 0;
    }
    .servises-item:hover img.hide-img {
        opacity: 1;
    }
}

.shadow {
    position: absolute;
    top: 0;
    right: 0;
    z-index: -1;
}

.cascade-slider_nav {
    display: none;
}

.works-slider-box {
    display: none;
}


/* 
    M O B I L E
*/

@media screen and (max-width: 1800px) {
    .top-img {
        right: 15px;
    }
}

@media screen and (max-width: 1620px) {
    .container {
        max-width: 1200px;
    }
    .top-img {
        display: flex;
        justify-content: flex-end;
        align-items: flex-end;
        img {
            width: 85%;
            margin-left: auto;
        }
    }
    .top-wrap h1 {
        font-size: 42px;
    }
    .servises-item:nth-child(4n) {
        margin-right: 30px;
    }
    .servises-item:nth-child(3n) {
        margin-right: 0;
    }
    .slick-initialized .slick-slide.works-slider__item {
        max-width: 500px;
    }
    .cascade-slider_container {
        max-width: 800px;
    }
    .cascade-slider_container {
        height: 336px;
    }
}

@media screen and (max-width: 1200px) {
    html,
    body {
        min-width: 320px;
        width: 100%;
        overflow-x: hidden;
    }
    a:focus {
        outline: none;
    }
    .servises-item {
        max-width: initial;
        width: 47.9%;
    }
    .servises-item:nth-child(3n) {
        margin-right: 30px;
    }
    .servises-item:nth-child(2n) {
        margin-right: 0;
    }
}

@media screen and (max-width: 1250px) {
    .top-wrap {
        min-height: 625px;
    }
    .duga img {
        width: 60%;
        margin-left: auto;
    }
    .top-img {
        img {
            width: 65%;
        }
    }
    .text-vertical {
        font-size: 140px;
        transform: rotate( -90deg) translateY(-250px);
        -webkit-transform: rotate( -90deg) translateY(-250px);
        -moz-transform: rotate( -90deg) translateY(-250px);
        -ms-transform: rotate( -90deg) translateY(-250px);
        -o-transform: rotate( -90deg) translateY(-250px);
    }
    .logo {
        margin-right: 35px;
    }
    .address {
        margin-right: 45px;
    }
    .logo {
        font-size: 20px;
        background-size: contain;
    }
    .cascade-slider_container {
        max-width: 650px;
    }
    .cascade-slider_arrow-right {
        right: -119px;
    }
    .cascade-slider_arrow-left {
        left: -119px;
    }
    .cascade-slider_container {
        height: 273px;
    }
}

@media screen and (max-width: 1140px) {
    .top-img img {
        width: 48%;
    }
    .top-wrap {
        padding-top: 50px;
    }
    .top-wrap h1 {
        font-size: 36px;
    }
    .top-wrap {
        min-height: 512px;
    }
    .text-vertical {
        font-size: 88px;
        top: 182px;
        transform: rotate( -90deg) translateY(-168px);
    }
    .top-wrap p {
        width: 50%;
        font-size: 16px;
    }
}

@media screen and (max-width: 1100px) {
    .address,
    .timework,
    .tel {
        font-size: 16px;
        background-size: contain;
    }
    .map {
        min-height: 343px;
    }
    footer {
        padding: 200px 0 0 0;
    }
    .map-contacts {
        top: 125px;
        padding: 27px 24px 6px 24px;
        background: #FFFFFF;
        max-width: 100%;
        left: 0;
        right: 0;
        width: 90%;
        margin: auto;
        box-shadow: 0px 4px 24px rgba(0, 0, 0, 0.1);
    }
    .map-contacts-item {
        margin-bottom: 23px;
    }
    .map-contacts-item span,
    .map-contacts-item a {
        font-size: 12px;
    }
}

@media screen and (max-width: 992px) {
    .copyright {
        padding-top: 28px;
        padding-bottom: 39px;
        flex-direction: column;
        align-items: flex-start;
    }
    .copyright a {
        display: block;
        margin-bottom: 20px;
        margin-right: 0;
    }
    .copyright .gen {
        margin-top: 20px;
        margin-left: 0;
    }
}

@media screen and (max-width: 985px) {
    #cascade-slider {
        display: none;
    }
    .works-slider-box {
        display: block;
        margin: 0 -16px;
    }
    .slick-initialized .slick-active.works-slider__item {
        margin: 0 3px;
        transform: scale(0.9);
        -webkit-transform: scale(0.9);
        -moz-transform: scale(0.9);
        -ms-transform: scale(0.9);
        -o-transform: scale(0.9);
    }
    .slick-initialized .slick-slide.works-slider__item {
        margin: 0 3px;
    }
    .slick-initialized .slick-current.works-slider__item {
        transform: scale(1);
        -webkit-transform: scale(1);
        -moz-transform: scale(1);
        -ms-transform: scale(1);
        -o-transform: scale(1);
    }
    .slick-initialized .slick-current.works-slider__item:after {
        opacity: 0;
    }
    .slick-dots {
        padding-left: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 10px;
        margin-top: 16px;
        li {
            @include tr;
            margin: 0 8px;
            background: #FF4C00;
            width: 6px;
            height: 6px;
            border-radius: 50%;
            -webkit-border-radius: 50%;
            -moz-border-radius: 50%;
